import { createRouter, createWebHistory } from 'vue-router'

import Home from '@/pages/Users.vue'


export const routers = createRouter({
    history: createWebHistory(),
    routes: [
        { path: '/', name: 'HomePage', component: Home},
        { path: '/contact', name: 'ContactPage', component: () => import('@/pages/Posts.vue')},
        { path: '/todos', name: 'Todos', component: () => import('@/pages/Todos.vue')},
        { path: '/:catchAll(.*)', name: '404', component: () => import('@/pages/NotFound.vue')},
        { path: '/users', name: 'Users', component: () => import('@/pages/Users.vue')},
        { path: '/post/:id', name: 'Posts', component: () => import('@/pages/PostsInfo.vue')},
        { path: '/users/:id', name: 'Users', component: () => import('@/pages/UserInfo.vue')},
    ]
})