import { createStore } from 'vuex'


export const store = createStore({
    state: {
        users: null,
        posts: null,
        todos: null
    },
    mutations: {
        getUsers(state, data) {
            state.users = data
        },
        getPosts(state, postData) {
            state.posts = postData
        },
        getTodos(state, todoData) {
            state.todos = todoData
        }
    },
    actions: {
        async getUsers(context) {
            let res = await fetch('https://jsonplaceholder.typicode.com/users')
            let data = await res.json()
            context.commit('getUsers', data)
        },
        async getPosts(context) {
            let res = await fetch('https://jsonplaceholder.typicode.com/posts')
            let postData = await res.json()
            context.commit('getPosts', postData)
        },
        async getTodos(context) {
            let res = await fetch('https://jsonplaceholder.typicode.com/todos')
            let todoData = await res.json()
            context.commit('getTodos', todoData)
        }
    },
    getters: {
        
    }
})