<template>
    <div class="container">
       <div class="main__items" v-if="users">
        <div class="main__items-item" v-for="user in users" :key="user.id">
            <img src="@/assets/images/profilePFP.png" alt="" class="profilePFP">
            <router-link :to="/users/ + user.id"><a>Name: {{ user.name }}</a></router-link>
            <p>Email: {{ user.email }}</p>
            <p>City: {{ user.address.city }}</p>
        </div>
       </div>
       <div v-else>
            Идет загрузка...
       </div>
    </div>
</template>

<script>
    export default {
        created() {
            this.getUsers()
        },
        computed: {
            users() {
                return this.$store.state.users
            },
        },
        methods: {
            getUsers() {
                this.$store.dispatch('getUsers') 
            }
        },
    }
</script>

